import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
import {
	getSearchHot,
	search
} from "@/api/search.js"
import {
	getSongUrl,
	getSongDetail
} from "@/api/song.js"
import {
	mapMutations
} from 'vuex'
export default {
	data() {
		return {
			defaultKeyword: "",
			keyword: "",
			oldKeywordList: [],
			hotKeywordList: [],
			keywordList: [],
			forbid: '',
			isShowKeywordList: false,
			searchResult: {
				code: 0
			}
		}
	},
	onLoad() {
		this.init();
	},
	components: {
		mSearch
	},
	methods: {
		...mapMutations(['setPlayingSong']),
		init() {
			this.loadDefaultKeyword();
			this.loadOldKeyword();
			this.loadHotKeyword();

		},
		blur() {
			uni.hideKeyboard()
		},
		//加载默认搜索关键字
		loadDefaultKeyword() {
			//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
			this.defaultKeyword = "默认关键字";
		},
		//加载历史搜索,自动读取本地Storage
		loadOldKeyword() {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					var OldKeys = JSON.parse(res.data);
					this.oldKeywordList = OldKeys;
				}
			});
		},
		//加载热门搜索
		async loadHotKeyword() {
			let res = await getSearchHot();
			this.hotKeywordList = res.data.map(item => {
				return item.searchWord;
			});
		},
		//监听输入
		inputChange(event) {
			//兼容引入组件时传入参数情况
			var keyword = event.detail ? event.detail.value : event;
			if (!keyword) {
				this.keywordList = [];
				this.isShowKeywordList = false;
				return;
			}
			// this.isShowKeywordList = true;
		},
		//高亮关键字
		drawCorrelativeKeyword(keywords, keyword) {
			var len = keywords.length,
				keywordArr = [];
			for (var i = 0; i < len; i++) {
				var row = keywords[i];
				//定义高亮#9f9f9f
				var html = row[0].replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
				html = '<div>' + html + '</div>';
				var tmpObj = {
					keyword: row[0],
					htmlStr: html
				};
				keywordArr.push(tmpObj)
			}
			return keywordArr;
		},
		//顶置关键字
		setKeyword(index) {
			this.keyword = this.keywordList[index].keyword;
		},
		//清除历史搜索
		oldDelete() {
			uni.showModal({
				content: '确定清除历史搜索记录？',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.oldKeywordList = [];
						uni.removeStorage({
							key: 'OldKeys'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		//热门搜索开关
		hotToggle() {
			this.forbid = this.forbid ? '' : '_forbid';
		},
		//执行搜索
		async doSearch(keyword) {
			this.keyword = keyword ? keyword : this.keyword;
			this.saveKeyword(keyword); //保存为历史 
			uni.showToast({
				title: "搜索 " + keyword + " ...",
				icon: 'loading',
				duration: 2000
			});
			let res = await search({
				keywords: this.keyword
			});
			this.searchResult = res.result;
		},
		//保存关键字到历史记录
		saveKeyword(keyword) {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					var OldKeys = JSON.parse(res.data);
					var findIndex = OldKeys.indexOf(keyword);
					if (findIndex == -1) {
						OldKeys.unshift(keyword);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(keyword);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				},
				fail: (e) => {
					var OldKeys = [keyword];
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				}
			});
		},
		async playSong(song) {
			await this.getUrl(song);
			let routes = getCurrentPages(); // 获取当前打开过的页面路由数组
			let curRoute = routes[routes.length - 1].route;
			uni.navigateTo({
				url: '/pages/songDetail/song-detail',
				success: r => {
				},
				fail: () => {},
				complete: () => {}
			});
		},
		async getUrl(song) {
			let artist = [];
			for (const ar of song.ar) {
				artist.push(ar.name);
			}
			let cover = await this.getCover(song.id);
			let songFmt = {
				id: song.id,
				name: song.name,
				artist: artist.join("/"),
				cover: cover,
				albumName: song.al.name,
				albumId: song.al.id,
				theme: [255, 255, 255]
			};
			this.setPlayingSong(songFmt);
			let res = null;
			getSongUrl(songFmt.id).then(res => {
				let songList = res.data.map(item => {
					return {
						url: item.url,
						id: item.id
					};
				});
				let url = songList[0].url;
				if (!url) {
					uni.showToast({
						title: '暂无版权',
						icon: 'none',
						mask: true,
					});
					return;
				}
				this.$bgAudioMannager.title = song.name;
				this.$bgAudioMannager.singer = song.artist;
				this.$bgAudioMannager.coverImgUrl = song.cover;
				this.$bgAudioMannager.src = url;
			}).catch(e => {
				console.log(e);
			});
		},
		async getCover(data) {
			let res = await getSongDetail(data);
			let song = res.songs[0];
			return song.al.picUrl;
		},
		goPlaylistDetail(id) {
			uni.navigateTo({
				url: '/pages/playlistDetail/playlist-detail?id=' + id,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
